<template>
  <div>
    <div class="wrapper">
      <div class="container" v-show="isShowButton">
        <div
          class="card"
          v-for="item in cardData"
          :key="item.value"
          @click="isDialog(item.label, item.value)"
        >
          <p>{{ item.title }}</p>
        </div>

        <div class="card" @click="goToMarkdown()">
          <p>markdown</p>
        </div>
      </div>
      <el-dialog :title="windowTitle" :visible.sync="dialogVisible" width="60%">
        <div class="resourcesPage" v-if="resources === 'font-end'">
          <Resources></Resources>
        </div>
        <div v-else-if="resources === 'douban'">豆瓣主页正在开发中</div>
      </el-dialog>
      <div style="overflow: auto; padding-bottom: 1rem">
        <router-view>
          <MarkdownFile></MarkdownFile>
        </router-view>
      </div>
    </div>
  </div>
</template>

<script>
import Resources from "./projects/Resources.vue";
import MarkdownFile from "./projects/MarkdownFile.vue";
export default {
  name: "",
  components: {
    Resources,
    MarkdownFile,
  },
  props: {},
  data() {
    return {
      dialogVisible: false,
      isShowButton: true,
      windowTitle: "",
      resources: "",
      cardData: [
        {
          title: "我的豆瓣",
          label: "豆瓣",
          value: "douban",
        },
        {
          title: "前端资源页",
          label: "前端资源",
          value: "font-end",
        },
      ],
    };
  },
  computed: {},
  watch: {},
  created() {},
  mounted() {},
  methods: {
    goToMarkdown() {
      this.isShowButton = false;
      this.$router.push({ path: "/project/markdown" });
    },
    isDialog(label, value) {
      this.dialogVisible = true;
      this.windowTitle = label;
      this.resources = value;
    },
  },
};
</script>

